import React from "react";
import { CustomInput } from "../commons/CustomTextarea";
import { Field, reduxForm } from "redux-form";
import {required, maxLength50} from '../../utils/validators/validators'
import { connect } from "react-redux";
import { Navigate } from "react-router-dom";
import { login } from "../../redux/auth-reducer";
import createField from "../commons/helpers/createField";
const Login = ({login, isAuth, createField}) => {
    const onSubmit = (formData) =>{
        login(formData.email, formData.password, formData.rememberMe)
    }
    if(isAuth){return <Navigate to='/profile'/>}
    return <div>
    <h3>LOGIN</h3>
     <LoginReduxForm createField= {createField} onSubmit={onSubmit}/>
    </div> 
}
const LoginForm =({handleSubmit, error}) =>{
    return(
        <form onSubmit={handleSubmit}>
    {createField([required, maxLength50], "login","email",CustomInput)}
    {createField([required, maxLength50], "password","password",CustomInput)}
    {createField(null, null,"rememberMe",CustomInput, {type:'checkbox'}, 'Remember me')}
   {error && <a>{error}</a>}
    <div>
        <button>Login</button>
    </div>
</form>
    )
}
const LoginReduxForm = reduxForm({form: "login"})(LoginForm) //form is a unique name именно той которую оборачиваю
const mapStateToProps = (state) =>{
    return{
        isAuth:state.auth.isAuth,
    }
}
export default connect(mapStateToProps, {login, createField})(Login);